<template>
  <div>
  <el-image-viewer v-if="showViewer" :on-close="closeViewer" :url-list="[url]" />
  <div style="display:flex;justify-content: space-between;margin-bottom:10px">
  <div style="display:flex">
    <span class="title" style="margin-left: 20px;">日期 :</span>
    <el-date-picker
      v-model="valueDate"
      type="daterange"
      range-separator="至"
      format="yyyy 年 MM 月 dd 日"
      value-format="yyyy-MM-dd 00:mm:ss"
      start-placeholder="开始日期"
      end-placeholder="结束日期">
    </el-date-picker>
  </div>
   <div>

  <el-button class="exportExcel"  @click="_exportExcel" style="margin-right: 20px;text-align:right;background:#77A1D3"  type="primary" icon="el-icon-download">導出Excel</el-button>
  <el-button class="exportExcel" @click="_addItem" style="margin-right: 20px;text-align:right;background:#20BDFF"  type="primary" icon="el-icon-plus">新增数据</el-button>
  <el-button type="primary" style="background:#3CA55C" icon="el-icon-search" @click="_search">搜索</el-button>

   </div>
</div>
    <el-table
     v-if ="tableData.length"
     v-loading = "pictLoading"
      element-loading-background = "rgba(0, 0, 0, 0.5)"
      element-loading-text = "数据正在加载中"
      element-loading-spinner = "el-icon-loading"
     :stripe= "true"
     :border="true"
    :data="tableData"
    highlight-current-row
    style="width: 100%">
    <el-table-column
      type="index"
      label="序號"
      width="55">
    </el-table-column>
    <!-- boardModel,boradType,status,num,imgUrl,description,owner,uploader -->
    <el-table-column
      property="boardModel"
      label="幾種"
     >
    </el-table-column>
    <el-table-column
      property="boardType"
      label="類型"
   >
   </el-table-column>
    <el-table-column
      property="num"
      label="總數量"
   ></el-table-column>
    <el-table-column
      property="status"
      label="狀態"
   ></el-table-column>
    <el-table-column
      property="imgUrl"
      label="主板圖片"
      style="cursor: pointer"
   >
    <template  slot-scope="scope">
          <img @click="showImg(scope.row.imgUrl)" v-if="scope.row.imgUrl" :src="scope.row.imgUrl"  width="100"  />
        </template>
   </el-table-column>
    <el-table-column
      property="description"
      label="說明"
   ></el-table-column>
    <el-table-column
      property="owner"
      label="掛賬人"
   ></el-table-column>
    <el-table-column
      property="uploader"
      label="上傳人員"
   ></el-table-column>
    <el-table-column
      property="createDate"
      label="創建時間"
   >
    </el-table-column>
      <el-table-column
      fixed="right"
      label="操作"
      width="100">
      <template slot-scope="scope">
        <el-button v-if="userInfo.username" @click="updateBoardInfo(scope.row)" type="text" size="small" slot="reference">修改</el-button>
        <el-popconfirm @confirm="handleClick(scope.row)" title="这是一段内容确定删除吗？">
            <el-button  v-if="userInfo.username" style="color: red" type="text"  size="small" slot="reference">删除</el-button>
        </el-popconfirm>
      </template>
    </el-table-column>
  </el-table>
  <BasePagination v-if="tableData.length" @handleCurrentChange="handleCurrentChange" @handleSizeChange="handleSizeChange" :total="total"/>
  <el-dialog
  title="EPS主板資料上傳"
  :close-on-click-modal="false"
  :visible.sync="dialogVisible"
  width="1200px"
  >
      <!-- boardModel,boradType,status,num,imgUrl,description,owner,uploader -->
  <div style="display:flex;justify-content:space-around">
    <div>
  <div style="margin-top: 10px">
    <span class="title">幾種 :</span><el-input v-model="needle.boardModel" style="width: 220px"></el-input>
  </div>
  <div style="margin-top: 10px;display:flex">
    <span class="title">類型 :</span><el-select v-model="needle.boardType" placeholder="请选择">
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
  </div>
  <div style="margin-top: 10px">
  <span class="title">狀態 :</span><el-select v-model="needle.status" placeholder="请选择">
    <el-option
      v-for="item in options1"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
  </div>
  <div style="margin-top: 10px">
    <span class="title">數量 :</span><el-input v-model="needle.num" type="number" style="width: 220px"></el-input>
  </div>
  <div style="display:flex;margin-top:10px">
    <span class="title">掛賬人 :</span><el-select v-model="needle.owner" placeholder="请选择">
    <el-option
      v-for="item in options2"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
  </div>
  <div style="display:flex;margin-top:10px">
    <span class="title">說明 :</span><el-input style="width: 220px" v-model="needle.description"></el-input>
  </div>
    <div style="display:flex;margin-top:10px">
    <span class="title">上傳人員 :</span><el-select v-model="needle.uploader" placeholder="请选择">
    <el-option
      v-for="item in options3"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
  </div>
    </div>
    <div>
      <div style="margin-top: 10px;text-align:center">
      <ImgCutter :rate="needle.rate" v-on:cutDown="cutDown" label="照片"></ImgCutter>
        <img style="margin-top: 20px" :src="imgUrl1" width="400px"  alt="照片">
      </div>
    <div style="display:flex;margin-top:10px">
    <span class="title">裁剪比例选择 :</span><el-select v-model="needle.rate" placeholder="请选择">
    <el-option
      v-for="item in options4"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
  </div>
    </div>
  </div>
  <div>
  </div>
  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="uploadIssueInfo">确 定</el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
/* eslint-disable */
import BasePagination from '@/components/base/basePagination'
import DataTable from '@/components/datatable'
import ImgCutter from 'vue-img-cutter'
import {uploadImage} from '../../api/upload'
import{ addboard, deleteboard, findEvery, findEveryAll, updateBoard} from '../../api/fae'
import {utc2beijing} from '../../utils/date'
import table2excel from 'js-table2excel'
import {mapState, mapGetters} from 'vuex'
import ElImageViewer from 'element-ui/packages/image/src/image-viewer'
export default {
  data () {
    return {
        //   <!-- boardModel,boardType,status,num,imgUrl,description,owner,uploader -->
      showViewer: false,
      url: '',
      imgUrl1: '',
      imgUrl2: '',
      dialogVisible: false,
      // 0为默认值，代表增加资料，1代表更新
      level: 0,
      options: [
        {
          value: '主板',
          label: '主板'
        }, {
          value: '小卡',
          label: '小卡'
        },
      ],
      options1: [
         {
          value: '正常',
          label: '正常'
        }, {
          value: '不正常',
          label: '不正常'
        },       
      ],
      options2: [
         {
          value: 'shundai',
          label: 'shundai'
        }, 
        {
          value: 'leedu',
          label: 'leedu'
        },   
        {
          value: '無賬',
          label: '無賬'
        },   
      ],
      options3: [
         {
          value: 'shundai',
          label: 'shundai'
        }, 
        {
          value: 'leedu',
          label: 'leedu'
        }
      ],
      options4: [
         {
          value: '1 : 1',
          label: '1 : 1'
        }, 
        {
          value: '2 : 1',
          label: '2 : 1'
        },
        {
          value: '3 : 1',
          label: '3 : 1'
        }
      ],
      needle: {
        boardModel: '',
        boardType: '主板',
        status: '正常',
        num: 1,
        imgUrl: '',
        description: '',
        owner: 'shundai',
        uploader: 'leedu',
        rate: '1 : 1'
      },
      valueDate: [],
      value: '',
      dialogVisible: false,
      // 员工工号
      pictLoading: false,
      tableData: [],
      currentPage: 1,
      pageSize: 10,
      userNum: '',
      startDate: '',
      total: 0,
    }
  },
  methods: {
    closeViewer () {
      this.showViewer = false
    },
    showImg (url) {
      this.url = url
      this.showViewer = true
    },
    updateBoardInfo (item) {
      this.level = 1
      this.needle = item
      this.imgUrl1 = item.imgUrl
      this.dialogVisible = true
    },
    handleClick (item) {
      let {_id} = item
      deleteboard({_id:_id,username: this.userInfo.username}).then(res => {
        if(res.data === 'error') {
          this.$message({
            message: '删除失败!',
            type: 'warning'
          })         
        }else if (res.data === 'ok') {
          this.$message({
            message: '删除成功!',
            type: 'success'
          })
          this._search()       
        }else{
          this.$message({
            message: '未知错误请联系管理员!',
            type: 'error'
          })        
        }
      }) 
    },
    uploadIssueInfo () {
       if (this.isLogin) {
         if (this.level === 0) {
            addboard(Object.assign({},this.needle, {username: this.userInfo.username})).then(res => {
              if (!res.data) {
                this.$message({
                  message: 'token过期，请重新登录',
                  type: 'warning'
                })
                this.dialogVisible = false
              }else{
                if (res.data === 'no_exist') {
                  this.$message({
                    message: '用户名不存在！',
                    type: 'warning'
                  })       
                }else if(res.data === 'isExist') {
    
                  this.$message({
                    message: '上傳主板已存在！',
                    type: 'warning'
                  })       
                } else {
                    this.$message({
                    message: '上傳成功!',
                    type: 'success'
                  })
                  this.dialogVisible = false
                  this._search()               
                }
              }
            }).catch(err=>{
              console.log(res)
           })
         } else if (this.level === 1) {
           updateBoard(this.needle).then(res => {
             this.dialogVisible = false
             if (res === 'ok') {
                this.$message({
                message: '更新成功!',
                type: 'success'
               })         
             } else {
               this.$message({
                message: '更新成功!',
                type: 'success'
               })            
             }
             this._search()
           }) 
         }
       } else {
        this.$message({
          message: '请登录',
          type: 'warning'
        })
       }

    },
    resultImage (data) {
      // console.log(data)
      let fd = new FormData()
      fd.append("image", data.blob);
      // console.log(fd)
      // fd.append("originalname", data.fileName)
      let that = this
      uploadImage(fd).then(res=> {
         that.issue.imgUrlRes =res.data.url
      })
      this.imgUrl2 =  data.dataURL
    },
    cutDown (data) {
      // console.log(data)
      let fd = new FormData()
      fd.append("image", data.blob);
      // console.log(fd)
      // fd.append("originalname", data.fileName)
       let that = this
      uploadImage(fd).then(res=> {
        that.needle.imgUrl =res.data.url
      })
      this.imgUrl1 =  data.dataURL
    },
    _addItem () {
      this.level = 0
      this.needle = {
        boardModel: '',
        boardType: '主板',
        status: '正常',
        num: 1,
        imgUrl: '',
        description: '',
        owner: 'shundai',
        uploader: 'leedu',
        rate: '1 : 1'
      }
      this.dialogVisible = true
    },
    _search () {
      // console.log(this.userInfo.username)
      this.pictLoading = true
      findEvery(this.pageSize,this.currentPage,JSON.stringify(this.valueDate)).then(res =>{
        res.data.data.forEach(item => {
          item.createDate = utc2beijing(item.createDate)
        });
        this.tableData = res.data.data
        // console.log(res.data)
        this.total = res.data.total
        this.pictLoading = false
      })
    },
    _exportExcel () {
      findEveryAll(JSON.stringify(this.valueDate)).then(res => {
        res.data.data.forEach(item => {
           item.imgUrl = item.imgUrl + item.rate.substring(0,1)
           item.createDate = utc2beijing(item.createTime)
        });
        // boardModel,boradType,status,num,imgUrl,description,owner,uploader
      const column = [
        {title: '幾種', key: 'boardModel', type: 'text'},
        {title: '主板圖', key: 'imgUrl', type: 'image'},
        {title: '類型', key: 'boardType', type: 'text'},
        {title: '總數量', key: 'num', type: 'text'},
        {title: '狀態', key: 'status', type: 'text'},
        {title: '說明', key: 'description', type: 'text'},
        {title: '掛賬人', key: 'owner', type: 'text'},
        {title: '上傳人員', key: 'uploader', type: 'text'},
        {title: '上傳時間', key: 'createTime', type: 'text'},
        
      ]
        const tabelName = this.valueDate.length > 1 ? `FAE寶安區主板&小卡資料統計表 ${this.valueDate[0].substring(0,10)}-${this.valueDate[1].substring(0,10)}.xls` :'FAE寶安區主板&小卡資料統計表.xls'
          // 下载excel选中表单内容
      
     if (this.tableData.length) {
        table2excel(column, res.data.data, tabelName)
      } else {
        this.$message({
          message: 'Please select at least one item',
          type: 'warning'
        })
      }  

      })


    },
    handleSizeChange (size) {
      this.currentPage = 1
      this.pageSize = size
      this._search()
    },
    handleCurrentChange (index) {
      this.currentPage = index
      this._search()
    },
  },
  mounted() {
    this._search()
  },
  computed: {
    ...mapState({
      isLogin: state => state.isLogin
    }),
    ...mapGetters([
      'userInfo'
    ])
  },
  components: {
    // DateAndTitle,
    BasePagination,
    DataTable,
    ImgCutter,
    ElImageViewer
  }

}
</script>

<style lang="scss" scoped>
.title {
  display: inline-block;
  height: 40px;
  line-height: 40px;
  color: #fff;
  width: 100px;
  text-align: center;
  font-size: 14px;
  padding: 0 5px;
  background: #409eff;
  // margin-left: 10px;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}
.idCon{
    display: inline-block;
    font-size: 0;
}
// 截图
.cropper-content {
    .cropper {
        width: auto;
        height: 300px;
    }
}
>>>.el-input__inner{
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
>>>.el-image-viewer__img{
  width: 300px;
  height: 300px;
}
</style>



