// 查詢issue數據
export function findEvery (pageSize , currentPage, dateArray) {
  // console.log(dateArray)
  return new Promise((resolve, reject) => {
    // console.log(userInfo)
    http.get(SERVER_HOST + FAE_BOARD__INFO_API + '?limit=' + pageSize+'&skip=' + (currentPage -1) * pageSize + '&dateArray=' + dateArray).then(res => {
      // console.log(res)
      resolve(res)
    }).catch(err => {
      // for Debug
      reject(err)
      console.log(err)
    })
  })
}

// 查詢issueall數據
export function findEveryAll (dateArray) {
  // console.log(dateArray)
  return new Promise((resolve, reject) => {
    // console.log(userInfo)
    http.get(SERVER_HOST + FAE_BOARD__INFO_ALL_API + '?dateArray=' + dateArray).then(res => {
      // console.log(res)
      resolve(res)
    }).catch(err => {
      // for Debug
      reject(err)
      console.log(err)
    })
  })
}


